import React, { useEffect, useState } from 'react';
import "./index.css";
import { Form, Input, Button, Checkbox, Row, Col, Card, Space } from 'antd';
import { Link } from 'react-router-dom'
import Api from '../../helpers/api';
import { connect } from 'react-redux';
import { setUser } from '../../redux/actions/setUserAction';
import store from "../../redux/store";
import { message } from "antd";

import {
    EyeOutlined,
    EyeInvisibleOutlined
} from '@ant-design/icons';

const api = new Api();

const Login = (props) => {

    const [form] = Form.useForm();
    const [errors, setErrors] = useState({})
    const [isLoading, setIsLoading] = useState(false)
    const [showPassword, setShowPassword] = useState(false)
    const [formData, setFormData] = useState({ email: "", password: "" })

    const handleInput = (e, name) => {
        setFormData({ ...formData, [name]: e.target.value })
    }

    const handleShowPassword = (e) => {
        setShowPassword(!showPassword)
    }

    const onFinish = () => {
        setIsLoading(true)
        api.login(formData)
            .then(response => {
                setIsLoading(false)
                if (response.status === 200) { //OK
                    try {
                        let user = response.data
                        let token = user.token
                        store.dispatch(setUser(user))

                        localStorage.setItem('user', JSON.stringify(user));
                        localStorage.setItem('api_token', token);
                        props.history.push('/')
                    } catch (error) {
                        console.log('Error save user data', error)
                    }
                }
            })
            .catch(error => {
                setIsLoading(false)
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setErrors(errors)
                    message.error(error.response.data.message);
                }
            });
    };

    return (
        <div className="d-lg-flex half">
            <div className="bg order-1 order-md-2 login-form-wrapper" />
            <div className="contents order-2 order-md-1">
                <div className="container" style={{ backgroundColor: '#fff' }}>
                    <div className="row align-items-center justify-content-center">
                        <div className="col-md-7">
                            <h3>Welcome Back!</h3>
                            <p className="mb-4">Login to continue.</p>

                            <Form form={form} onFinish={onFinish}>
                                <div className="form-group first">
                                    <label>Email  <span style={{ color: 'red' }}>{errors && errors.email ? errors.email : null}</span></label>
                                    <input
                                        onChange={value => handleInput(value, 'email')}
                                        type="text"
                                        className="form-control"
                                        placeholder="your-email@mail.com" />
                                </div>
                                <div className="form-group last mb-3">
                                    <label>Password  <span style={{ color: 'red' }}>{errors && errors.password ? errors.password : null}</span></label>
                                    <Row>
                                        <Col md={22}>
                                            <input
                                                onChange={value => handleInput(value, 'password')}
                                                type={showPassword ? 'text' : 'password'}
                                                className="form-control" placeholder="Your Password" />
                                        </Col>
                                        <Col md={2}>
                                            <Button
                                                onClick={handleShowPassword}
                                                type="link"
                                                icon={
                                                    showPassword ?
                                                        <EyeInvisibleOutlined style={{ color: 'black' }} /> :
                                                        <EyeOutlined style={{ color: 'black' }} />
                                                }
                                            />
                                        </Col>
                                    </Row>
                                </div>
                                <div className="d-flex mb-5 align-items-center">
                                    <Checkbox >Remember me</Checkbox>
                                    <span className="ml-auto">
                                        <Link className="forgot-pass" to="/forgot-password">Forgot password</Link>
                                    </span>
                                </div>

                                <Button
                                    size="large"
                                    block
                                    type="primary"
                                    htmlType="submit"
                                    loading={isLoading}>
                                    Login
                                </Button>
                            </Form>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default connect(
    null,
    { setUser }
)(Login);
